@font-face {
  src: url('./assets/fonts/Quicksand-Medium.ttf');
  font-family: 'font';
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f4f4f4;
  background-size: cover;
  background-image: url('./assets/images/ToodoBg.png');
  background-position: top;
}

* {
  font-family: 'font' !important;
  user-select: none;
  transition: all ease 0.3s;
}

*::-webkit-scrollbar {
  background: #f4f4f4;
  width: 6px;
  border-radius: 30px;
}

*::-webkit-scrollbar-thumb {
  background: #c0c0c0;
  border-radius: 10px;
  transition: all ease 0.2s !important;
}

*::-webkit-scrollbar-thumb:hover {
  background: #3f51b5;
}

a {
  text-decoration: none !important;
  color: inherit;
}

select {
  border: 0px solid white;
  background: transparent;
  border-bottom: 1px solid #0000006b;
  padding: 6px 0px 7px;
  font-size: 16px;
}

.main-text {
  font-size: 20px;
  font-weight: bold;
  color: inherit;
  padding: 2px 5px;
  border-radius: 4px;
}

.txt-center {
  color: white;
  text-align: center;
  text-shadow: 0px 1px 2px black, 0px -1px 2px black, -1px 0px 2px black,
    1px 0px 2px black;
}

.bold {
  font-weight: bold;
}

.pt-10 {
  padding-top: 10px;
}

.pb-10 {
  padding-bottom: 10px;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.m-text {
  font-size: 0px;
}
.m-text:hover {
  font-size: 13px;
}

.nooverflow {
  overflow: hidden;
  max-width: 250px;
  margin: none;
}

/* AppHeader */
.app-header {
  top: 10px;
  right: 10px;
  position: fixed;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background: #1a1a2e;
  color: white;
  border-radius: 10px;
  padding: 20px 10px;
  border: 2px solid white;
  z-index: 50000;
}

.app-header:hover {
  align-items: center;
}

.app-header:hover .main-text {
  font-size: 34px;
}

.app-header:hover .m-text {
  font-size: small;
  padding: 0px 10px 0px 0px;
}

.header-btn-container {
  display: flex;
  flex-flow: column;
}

.button-add {
  color: white;
  width: 100%;
  background: transparent;
  color: white;
  outline: none;
  border: 0px solid transparent;
  font-weight: bold;
}

.button-add:hover {
  background: #f4f4f4;
  color: black;
}

.login {
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100vw;
  height: 100vh;
}

.login-card {
  background: #1a1a2e;
  display: flex;
  flex-flow: column;
  padding: 30px 20px;
  margin: 30px 20px;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 3px 5px grey;
  border-radius: 10px;
  border: 2px solid white;
  width: 90%;
  max-width: 600px;
  height: 82vh;
  color: white;
}

.flex-grow {
  flex-grow: 1;
}

.projects-list {
  min-width: 200px;
  display: flex;
  flex-flow: row wrap;
  padding: 10px 10px 10px 10px;
}

.center-container {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  text-align: center;
  padding: 10px;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  z-index: 1000000;
}

.modal-content {
  background: white;
  min-width: 400px;
  padding: 20px;
  border-radius: 10px;
}

.loader {
  background: white;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.mini-container {
  padding: 20px 0px;
}

.btn-container {
  padding: 30px 0px 0px 0px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

@media screen and (max-width: 600px) {
  .modal-content {
    min-width: 0px;
    background: white;
    width: 80%;
    height: 90%;
    padding: 20px;
    border-radius: 10px;
  }
}
